import { saveToken, getToken, redirectTo, fetchUrl } from "../util";
import {
  XANO_USER_DASHBOARD_REDIRECT,
  XANO_USER_UPDATE_PASSWORD_URL,
  XANO_USER_MAGIC_TOKEN_TO_AUTH_TOKEN_URL,
} from "../util/xano_urls";

// ? utils
async function convertMagicTokenToAuthToken(magicToken) {
  const xano_input = {
    magic_token: magicToken,
  };
  let token = await fetchUrl(XANO_USER_MAGIC_TOKEN_TO_AUTH_TOKEN_URL, "POST", xano_input);

  if (token) {
    saveToken(token);
  }
}

window.onload = function () {
  // ? get reset token from url, that was found via email link
  var curUrl = new URL(document.location.href);
  var token = curUrl.searchParams.get("token");
  if (token) {
    convertMagicTokenToAuthToken(token);
  }
};

// ? login user via xano after they submit form
// ? form submission workaround
var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */
  $("form").submit(async function (e) {
    e.preventDefault();
    const $form = $(this); // The submitted form
   const token = getToken();

    const xano_input = {
      password: document.getElementById("password").value, // ? bcrypt
      confirm_password: document.getElementById("confirm-password").value,
    };
    let data = await fetchUrl(XANO_USER_UPDATE_PASSWORD_URL, "POST", xano_input, token);

    if (data.isFetchOk !== undefined && !data.isFetchOk) {
      $form
        .siblings(".w-form-done")
        .hide() // Hide success
        .siblings(".w-form-fail")
        .show(); // show failure

      console.error(data.message);
      return;
    } else {
      $form
        .hide() // optional hiding of form
        .siblings(".w-form-done")
        .show() // Show success
        .siblings(".w-form-fail")
        .hide(); // Hide failure
    }

    redirectTo(XANO_USER_DASHBOARD_REDIRECT);
  });
});
